*{
    margin: 0;
    padding: 0;
  }
  
  .container{
    background: linear-gradient(to right bottom, rgb(6, 112, 62),white);
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .app_wrapper{
    background-color: #05513a;
    min-width: 450px;
    min-height: 650px;
    padding: 30px;
    box-sizing: border-box;
    border-radius: 10px;
    margin-bottom: 10px;
  }
  .header h1{
    color: #fff;
    text-align: center;
    margin: 30px 0;
  }
  .task_input {
    outline: none;
    width: 260px;
    padding: 15px;
    margin-right: 25px;
    font-size: 20px;
    color: black;
    background-color: #4fac90;
    border: 1px solid black;
    border-radius: 10px;
  }
  .task_input::placeholder{
    color: black;
  }
  .button_add{
    width: 70px;
    padding: 15px;
    font-size: 20px;
    border-radius: 10px;
    border: none;
    background-color: #4fac90;
    font-weight: bold;
    cursor: pointer;
  }
  .button_add:hover{
    background-color: green;
  }
  .button_add:active{
    background-color: #4fac90;
  }
  .list_item {
    display: flex;
    margin: 20px 0;
    border: 1px solid #ccc;
    border-radius: 10px;
    padding: 10px;
    max-height: 30px;
  }
  .button_complete, .button_delete, .button_edit{
    border: none;
    outline: none;
    cursor: pointer;
    background-color: transparent;
    font-size: 28px;
    border-radius: 50%;
  }
  
  .button_complete{
    color: #ff6c6c;
    margin-right: 10px;
  }
  
  .button_edit{
    color: white;
    margin-right: 10px;
  }
  .button_delete {
    color: lightseagreen;
  }
  .complete{
    text-decoration-style: solid;
    text-decoration-line: line-through;
    text-decoration-color: white;
    opacity: 0.6;
  }
  .list{
    width: 220px;
    background-color: #05513a;
    border: none;
    color: #ccc;
    font-size: 20px;
    padding-left: 10px;
    margin-right: 15px;
  }
  input:focus, button:focus {
    outline: none;
  }